<template>
  <scroll-view class="scroll-view">
    <div class="list">
      <div class="list-item" v-for="(e, i) in collection" :key="i">
        <p @click="play(i)">{{e.song}}<span>{{e.singer}}</span></p>
        <van-icon name="cross" @click="removeCollection(i)"/>
      </div>
    </div>
    <div v-if="!collection.length" class="none">没有收藏~</div>
  </scroll-view>
</template>
<script>
import { mapState, mapMutations, mapActions } from 'vuex'
import ScrollView from '#/ScrollView'

export default {
  name: 'Collection',
  components: {
    ScrollView,
  },
  computed: {
    ...mapState('music', ['collection'])
  },
  methods: {
    ...mapMutations('music', ['removeCollection']),
    ...mapMutations('player', ['songList']),
    ...mapActions('player', ['play']),

  },
}
</script>
<style lang="scss" scoped>
/deep/ .van-icon:before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }
/deep/ .van-icon {
  flex: 0 0 .6rem;
  font-size: .35rem; }
.scroll-view {
  height: calc(100vh - 44px);
}
.none {
  font-size: .3rem;
  text-align: center;
  padding: .2rem; }
.list {
  background-color: #fff;
  &-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #eee;
    padding: 0 .2rem;
    p {
      flex: auto;
      font-size: .28rem;
      padding: .25rem;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      span {
        color: #aaa;
        margin-left: .2rem;
      }
    }
  }
}
</style>


